import React from 'react'
import {
    Card,
    CardBody,
    FormGroup,
    Label,
    Input,
    Row,
    Col,
    Button,
    CustomInput
} from "reactstrap"
export default function AllBookingList() {
    return (
        <Card>
            <CardBody>
                <form>
                    <Row>
                        <Col lg="6" md="6" sm="12">
                            <FormGroup>
                                <Label> Courier Office </Label>
                                <CustomInput type="select" name="select" id="city">
                                    <option>Office No 1</option>
                                    <option>Chicago</option>
                                    <option>San Francisco</option>
                                    <option>Boston</option>
                                </CustomInput>
                            </FormGroup>
                        </Col>
                        <Col lg="6" md="6" sm="12">
                            <FormGroup>
                                <Label> Booking Office </Label>
                                <CustomInput type="select" name="select" id="city">
                                    <option>Booking Office</option>
                                    <option>Chicago</option>
                                    <option>San Francisco</option>
                                    <option>Boston</option>
                                </CustomInput>
                            </FormGroup>
                        </Col>
                        <Col lg="6" md="6" sm="12">
                            <FormGroup>
                                <Label for="">Reference #</Label>
                                <Input type="text" id="" placeholder="Enter Reference #" required />
                            </FormGroup>
                        </Col>
                        <Col lg="6" md="6" sm="12">
                            <FormGroup>
                                <Label> Account # </Label>
                                <CustomInput type="select" name="select" id="city">
                                    <option>Account #</option>
                                    <option>Chicago</option>
                                    <option>San Francisco</option>
                                    <option>Boston</option>
                                </CustomInput>
                            </FormGroup>
                        </Col>
                        <Col lg="6" md="6" sm="12">
                            <FormGroup>
                                <Label> Order Type </Label>
                                <CustomInput type="select" name="select" id="city">
                                    <option>Local</option>
                                    <option>Domestic</option>
                                    <option>International</option>
                                </CustomInput>
                            </FormGroup>
                        </Col>
                        <Col lg="6" md="6" sm="12">
                            <FormGroup>
                                <Label> Origin </Label>
                                <CustomInput type="select" name="select" id="city">
                                    <option>Local</option>
                                    <option>Domestic</option>
                                    <option>International</option>
                                </CustomInput>
                            </FormGroup>
                        </Col>
                        <Col lg="6" md="6" sm="12">
                            <FormGroup>
                                <Label> Destination </Label>
                                <CustomInput type="select" name="select" id="city">
                                    <option>Local</option>
                                    <option>Domestic</option>
                                    <option>International</option>
                                </CustomInput>
                            </FormGroup>
                        </Col>
                        <Col lg="6" md="6" sm="12">
                            <FormGroup>
                                <Label for="">Zone</Label>
                                <Input type="text" id="" placeholder="Zone" maxLength={11} required />
                            </FormGroup>
                        </Col>
                        <Col lg="6" md="6" sm="12">
                            <FormGroup>
                                <Label> Weight Type </Label>
                                <CustomInput type="select" name="select" id="city">
                                    <option>Actual weight</option>
                                    <option>Volumetric weight</option>
                                </CustomInput>
                            </FormGroup>
                        </Col>
                        <Col lg="6" md="6" sm="12">
                            <FormGroup>
                                <Label for="">Zone</Label>
                                <Input type="text" id="" placeholder="Zone" maxLength={11} required />
                            </FormGroup>
                        </Col>
                        <Col lg="6" md="6" sm="12">
                            <FormGroup>
                                <Label for="">Booking Type</Label>
                                <CustomInput type="select" name="select" id="city">
                                    <option>Actual weight</option>
                                    <option>Volumetric weight</option>
                                </CustomInput>
                            </FormGroup>
                        </Col>
                        <Col lg="6" md="6" sm="12">
                            <FormGroup>
                                <Label for="">Pices/Boxes</Label>
                                <Input type="text" id="" placeholder="Pices/Boxes" required />
                            </FormGroup>
                        </Col>
                        <Col lg="6" md="6" sm="12">
                            <FormGroup>
                                <Label for="">Service Type</Label>
                                <CustomInput type="select" name="select" id="city">
                                    <option>Actual weight</option>
                                    <option>Volumetric weight</option>
                                </CustomInput>
                            </FormGroup>
                        </Col>
                        <Col lg="6" md="6" sm="12">
                            <FormGroup>
                                <Label for="">Sender (Shipper/Consigner)</Label>
                                <Input type="text" id="" placeholder="Sender (Shipper/Consigner)" required />
                            </FormGroup>
                        </Col>
                        <Col lg="6" md="6" sm="12">
                            <FormGroup>
                                <Label for="">Receiver (Consigner)</Label>
                                <Input type="text" id="" placeholder="Receiver (Consigner)" required />
                            </FormGroup>
                        </Col>
                        <Col lg="6" md="6" sm="12">
                            <FormGroup>
                                <Label for="">Insurance</Label>
                                <CustomInput type="select" name="select" id="city">
                                    <option>Yes</option>
                                    <option>No</option>
                                </CustomInput>
                            </FormGroup>
                        </Col>
                        <Col lg="6" md="6" sm="12">
                            <Button.Ripple color="primary" type="submit">Insure Calculator </Button.Ripple>
                        </Col>
                        <Col lg="6" md="6" sm="12">
                            <FormGroup>
                                <Label for="">COD</Label>
                                <CustomInput type="select" name="select" id="city">
                                    <option>Yes</option>
                                    <option>No</option>
                                </CustomInput>
                            </FormGroup>
                        </Col>
                        <Col lg="6" md="6" sm="12">
                            <Button.Ripple color="primary" type="submit">COD Detail </Button.Ripple>
                        </Col>
                        <Col lg="6" md="6" sm="12">
                            <FormGroup>
                                <Label for="">Remote Area Charges</Label>
                                <Input type="text" id="" placeholder="Remote Area Charges" required />
                            </FormGroup>
                        </Col>
                        <Col lg="6" md="6" sm="12">
                            <FormGroup>
                                <Label for="">Insurance Charges</Label>
                                <Input type="text" id="" placeholder="Insurance Charges" required />
                            </FormGroup>
                        </Col>
                        <Col lg="6" md="6" sm="12">
                            <FormGroup>
                                <Label for="">Basic Charges</Label>
                                <Input type="text" id="" placeholder="Basic Charges" required />
                            </FormGroup>
                        </Col>
                        <Col lg="6" md="6" sm="12">
                            <FormGroup>
                                <Label for="">Remote Area Charges</Label>
                                <Input type="text" id="" placeholder="Remote Area Charges" required />
                            </FormGroup>
                        </Col>
                        <Col lg="6" md="6" sm="12">
                            <FormGroup>
                                <Label for="">Total Charges</Label>
                                <Input type="text" id="" placeholder="Total Charges" required />
                            </FormGroup>
                        </Col>
                        <Col lg="6" md="6" sm="12">
                            <FormGroup>
                                <Label for="">Discount %</Label>
                                <Input type="text" id="" placeholder="Discount %" required />
                            </FormGroup>
                        </Col>
                        <Col lg="6" md="6" sm="12">
                            <FormGroup>
                                <Label for="">Discounted Amount</Label>
                                <Input type="text" id="" placeholder="Discounted Amount" required />
                            </FormGroup>
                        </Col>
                        <Col lg="6" md="6" sm="12">
                            <FormGroup>
                                <Label for="">Payable Amount</Label>
                                <Input type="text" id="" placeholder="Payable Amount" required />
                            </FormGroup>
                        </Col>
                        <Col lg="6" md="6" sm="12">
                            <FormGroup>
                                <Label for="">Paid Amount</Label>
                                <Input type="text" id="" placeholder="Paid Amount" required />
                            </FormGroup>
                        </Col>
                        <Col lg="6" md="6" sm="12">
                            <FormGroup>
                                <Label for="">Balance Amount</Label>
                                <Input type="text" id="" placeholder="Balance Amount" required />
                            </FormGroup>
                        </Col>

                    </Row>
                    <Button.Ripple color="primary" type="submit">Submit </Button.Ripple>
                </form>
            </CardBody>
        </Card>
    )
}
